import fs from 'fs';
import path from 'path';
import type { Template } from './definitions';

export async function getTemplates(): Promise<Template[]> {
  const filePath = path.join(process.cwd(), 'src', 'lib', 'data', 'templates.json');
  try {
    const jsonData = await fs.promises.readFile(filePath, 'utf-8');
    // Handle empty file
    if (!jsonData) {
      return [];
    }
    const data: { [category: string]: Template[] } = JSON.parse(jsonData);

    if (typeof data !== 'object' || data === null || Array.isArray(data)) {
        console.error("Error: templates.json is not a category-grouped object.");
        return [];
    }

    const allTemplates = Object.values(data).flat();
    
    // Assign originalId for easier editing/deleting
    return allTemplates;

  } catch (error) {
    console.error("Error reading or parsing templates.json:", error);
    if ((error as NodeJS.ErrnoException).code === 'ENOENT') {
      console.log('templates.json not found. Returning empty array.');
      return [];
    }
    console.error("Parsing error in templates.json:", error);
    return [];
  }
}
